<template>
  <div>
    <div class="c-panel" v-for="item in items" :key="item.id">
      <div class="c-panel__content">
        <h4 class="c-panel__title">{{ item.name }}</h4>
        <p class="c-panel__txt" style="white-space: pre-wrap;" v-text="item.content"></p>
      </div>
      <div class="c-panel__imgWrap">
        <img :src="item.image" alt="step_image" class="c-panel__img" />
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      items: []
    };
  },
  methods: {
    request() {
      axios.get("mypage/list").then(res => {
        let result = res.data;
        this.items = result;
      });
    }
  },
  created() {
    this.request();
  }
};
</script>
